export default function ajaxrequest(type,url){
   
    // return fetch(url,)
    // .then(res=>res.json())

    return fetch(url, {
        method: type,
        headers: {
            'X-CSRF-TOKEN': '{{ csrf_token() }}'
        }
        //body: data
      })
      .then(res=>res.json()) 
 }
