<template>
<div class="login">
  <div class="wrapper">
    <div class="sct brand">
        <img src="http://localhost/readsea/public/frontent/dist/redsea.png" width="400">
    </div>
    <div style="flex-direction: column;" class="sct login">
        <h3 id="msgerror">{{msg}}</h3>
        <form method="POST">
            <h3>ADMIN Login</h3>
            <input v-model="user" type="text" id="user"  name="username" placeholder="Username" required>
            <input v-model="pass" type="password" id="pass" name="password" placeholder="Password" required>
            <!--<div class="forgot-remember">
                    <label class="control control-checkbox">
                            Remember me
                                <input type="checkbox" />
                            <div class="control_indicator"></div>
                        </label>
                <div class="forgot">
                        <a href="#">Forgot Password?</a>
                </div> 
            </div>--->
            <input @click="ingresar($event)" type="submit" id="gologin" name="login" value="Login">
            <!--
            <p class="text-center">Sign up with<br><i class="fa fa-hand-o-down" aria-hidden="true"></i></p>
            <div class="social-sign">
                <a href="#"><i class="fa fa-facebook-square" aria-hidden="true"></i></a>
                <a href="#"><i class="fa fa-twitter-square" aria-hidden="true"></i></a>
                <a href="#"><i class="fa fa-linkedin-square" aria-hidden="true"></i></a>
            </div>-->
        </form>
    </div>
    <!--end login-->
</div>
<!--end wrapper-->
</div>    

</div>
</template>
  
  <script>
  import ajaxrequest from '../api/ajax.js'
  export default {
    name: 'login',
    data () {
    return {
      user: '',
      pass:'',
      logindata:{},
      msg:'',
      cont:1
    }
  },
  methods: {
    ingresar: function (event) {
        event.preventDefault()
        const self = this
        self.msg=''
         //'http://localhost/readsea/public/login?user='+self.logindata.user+'&pass='+self.logindata.pass
         if(self.user!='' && self.pass!='' && !localStorage.getItem("reset") )
         {
            self.logindata.user=self.user
            self.logindata.pass=self.pass
            console.log(self.user +' '+ self.pass)
            ajaxrequest('get','http://localhost/readsea/public/login?user='+self.logindata.user+'&pass='+self.logindata.pass)
            .then (function (data){
                if(data.status=="error"){
                    if(localStorage.getItem("intents")) {
                      self.cont=parseInt(localStorage.getItem("intents"));
                      self.cont=self.cont+1;
                    }
                    localStorage.setItem("intents",self.cont);

                    if(self.cont==3){
                      self.msg="Incorrect Username or Password. an email was sent to reset the password"
                      localStorage.setItem("reset","reset");
                    }else{
                      self.msg='Incorrect Username or Password. Try again'
                    }
                }else if(data.status=="ok"){
                  localStorage.removeItem("intents");
                  self.cont=1
                  self.msg=''
                  localStorage.setItem("logged","logged");
                  window.location.href = "http://redseaexperience.co.uk/admin/admin_1/html/ltr/vertical-menu-template-nav-dark/index.html";
                }
            })
            .catch(function (error) {
              console.log('Request failed', error);
            });
          }else if((self.user=='' || self.pass=='') && !localStorage.getItem("reset")) {
            self.msg="Please Enter Credentials"
          }else if (localStorage.getItem("reset")=="reset"){
            self.msg="You should reset your password, an email was sent to reset it"
          }
      },
    },
  }
  </script>
  
<style>
.login {
  height: 100%;
}

#msgerror{
  width: 85%;
  text-align: center;
  margin-bottom: 26px;
}

</style>
  